[v-cloak]>* {
	visibility: hidden;
}

[v-cloak]::before {
	content: "loading awesomeness...";
	padding: 200px 0;
	display: block;
	background: url("data:image/gif;base64,") no-repeat center 40%;
}

.rafflepressloading>* {
	visibility: hidden;
}

.rafflepressloading::before {
	content: "loading awesomeness...";
	padding: 200px 0;
	display: block;
	background: url("data:image/gif;base64,") no-repeat center 40%;
}

.iframe_loading {
